.skills-container {
	/*	width: 50vw;*/
	max-width: 100%;
	height: auto;
	/*	border: 1px solid black;*/
	margin: auto;
	margin-top: 10px;
	padding: 10px;
	font-size: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.skill-img {
	max-width: 50px;
	height: 50px;
	margin: 10px;
}
/*
