.rocket-card-container {
	max-width: 100%;
	height: auto;
	/*	border: 1px double black;*/
	display: flex;
	justify-content: center;
	align-content: center;
	word-break: break-word;
	padding: 10px;
}

.rocket-img-container {
	width: 60%;
	height: auto;
	/*	border: 1px solid black;*/
}

.rocket-card-info {
	width: 40%;
	height: auto;
	/*	border: 1px solid black;*/
	text-align: center;
}

#rocket-img {
	width: 100%;
	height: 100%;
	animation: rocket-animation 2s ease-in-out infinite;
}

@keyframes rocket-animation {
	0% {
		transform: translateY(0) scale(1);
	}
	50% {
		transform: translateY(-20px) scale(1.1); /* Move up and slightly scale */
	}
	100% {
		transform: translateY(0) scale(1);
	}
}

@media screen and (max-width: 567px) {
	.rocket-card-container {
		width: 100%;
		height: auto;
		font-size: 0.8rem;
	}

	.rocket-card-info {
		height: auto;
	}
}
